.news_page {
    width: 100%;
    height: 100%;
    text-overflow: scroll;
}

.infinite-scroll-component__outerdiv{
    width: 90%;
    float: right;
}

.news_page .navbar_auth p {
    text-align: center;
    font-size: 1.7rem;
    margin: 20px 40%
}

.main_news {
    width: 100%;
}

.post_data_button {
    position: fixed;
    bottom: 10px;
    right: 10px;
    background-color: black;
    cursor: pointer;
    border-radius: 50%;
    width: 50px;
    height: 50px;
}

.post_data_button:active {
    border: none
}

.post_data_button:hover {
    background-color: rgba(46, 229, 157, 0.4);
}

.post_data .MuiSvgIcon-root {
    margin: 0;
    padding: 5px;
}

.post_data .MuiSvgIcon-root:hover {
    color: black
}

.form_post_blog {
    background-color: #242526;
    color: white;
    width: 700px;
    padding: 20px 50px;
}
#form_post_blog_div{
    height: max-content;
    display:flex;
    margin-bottom: 10px;
}
.form_post_blog .input_extension{
    width: 5%;
    margin-right: 20px;
    background-color: #3a3b3c;
    text-align: center;
    border-radius: 10px;
}
.form_post_blog .input_extension button{
    padding:0
}
.form_post_blog section, .navbar_modal_body section {
    height: 100px;
    border-style: dashed;
    border-color: rgb(100, 100, 241);
}

.form_post_blog p {
    height: 100%;
    margin: 0;
    padding: 0 10%;
}

.form_post_blog h4 {
    text-align: center;
    font-weight: 500;
    font-size: 2rem;
    margin: 0px;
    margin-bottom: 20px;
}


.drag_file {
    outline: 0px solid transparent;
    height: 100%;
}

.drag_file .icon {
    width: 100%;
    height: 100%;
}

.icon svg {
    background-color: cadetblue;
    margin-top: 0;
    padding: 15px;
    border-radius: 50%;
}

#type_text_div{
    border-radius: 5px;
    box-sizing: border-box;
    width: 100%;
    margin-bottom: 10px;
    resize:none;
    box-shadow: none;
    background-color: transparent;
    padding: 5px 10px;
    flex: 1 1 0%;
    overflow: hidden;
    height: 200px;
    overflow-wrap: break-word;
    color: white;
    border: 1px solid #3a3b3c;
}
#type_text_div span{
    word-break: break-all;

}
#type_text_div::-webkit-scrollbar, .image_post_news::-webkit-scrollbar, .post_viewing .content::-webkit-scrollbar, #menuContainerMessage .MuiPaper-root::-webkit-scrollbar {
    width: 5px;
    height: 5px;
    border: 0px;
}

#type_text_div::-webkit-scrollbar-thumb, .image_post_news::-webkit-scrollbar-thumb, .post_viewing .content::-webkit-scrollbar-thumb, #menuContainerMessage .MuiPaper-root::-webkit-scrollbar-thumb {
    background: #000000;
    border: 0px none #ffffff;
    border-radius: 50px;
}

#type_text_div::-webkit-scrollbar-thumb:hover, .image_post_news::-webkit-scrollbar-thumb:hover, .post_viewing .content::-webkit-scrollbar-thumb:hover, #menuContainerMessage .MuiPaper-root:hover {
    background: black;
}

#type_text_div::-webkit-scrollbar-thumb:active, .image_post_news::-webkit-scrollbar-thumb:active, .post_viewing .content::-webkit-scrollbar-thumb:active, #menuContainerMessage .MuiPaper-root:active {
    background: #000000;
}

#type_text_div::-webkit-scrollbar-track:hover, .image_post_news::-webkit-scrollbar-track:hover, .post_viewing .content::-webkit-scrollbar-track:hover, #menuContainerMessage .MuiPaper-root:hover {
    background: white;
}

#type_text_div::-webkit-scrollbar-track:active, .post_viewing .content::-webkit-scrollbar-track, .image_post_news::-webkit-scrollbar-track, #menuContainerMessage .MuiPaper-root::-webkit-scrollbar-track:active {
    background: white;
    ;
}

#type_text_div::-webkit-scrollbar-corner, .image_post_news::-webkit-scrollbar-corner, .image_post_news::-webkit-scrollbar-corner, #menuContainerMessage .MuiPaper-root::-webkit-scrollbar-corner {
    background: transparent;
}

[contenteditable] {
    outline: 0px solid transparent;
}

.image_post_news {
    padding-bottom: 5px;
    overflow-x: scroll;
    width: 100%;
    margin-top: 10px;
}

.image_post_news li {
    list-style-type: none;
    margin-right: 10px;
    position: relative;
}

.image_post_news img {
    width: 100px;
    height: 100px;
}

.image_post_news span {
    position: absolute;
    right: 0;
    font-size: 2rem;
    color: black;
}

.image_post_news span:hover {
    color: rgba(204, 36, 14, 0.993);
}

.extension_auth {
    margin-left: auto;
    margin-right: 55px;
}

.extension_auth .MuiSvgIcon-root {
    font-size: 2rem;
}

.infinite-scroll-component {
    background-color: #181818;
}

.comment_a_blog .infinite-scroll-component {
    background-color: inherit;
}

.profile img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.profile_text {
    padding-left: 10px;
    padding-top: 10px;
}

.profile_text span {
    font-size: 1.1em;
    color: #fbff08;
    text-shadow: 0 0 1px #fbff08, 0 0 1px #fbff08, 0 0 1px #fbff08, 0 0 1px #fbff08, 0 0 1px #fbff08;
}

.profile_text span:after {
    position: absolute;
    top: 0;
    left: 0;
    padding: 0 1px;
    z-index: -1;
    filter: blur(0.5px);
}

.profile_text span:before {
    content: '';
    background: #fbff08;
    z-index: -2;
    opacity: .5;
    filter: blur(2px);
}

.message_icon .MuiSvgIcon-root:hover {
    filter: drop-shadow(2px 5px 5px rgba(228, 231, 230, 0.959));
    transition: all 0.3s ease 0s;
}

.message_icon svg {
    color: rgba(225, 230, 228, 0.89)
}

.icon_nav svg {
    color: rgba(10, 243, 150, 0.89)
}

.icon_nav .MuiSvgIcon-root:hover {
    filter: drop-shadow(2px 5px 5px rgba(10, 243, 150, 0.89));
    transition: all 0.3s ease 0s;
}
.skeleton_div{
    margin-top: 50px;
    width: 45%;
    position: relative;
    left: 24%;
}
#recal{
    margin-left: 0;
    margin-right: auto;
    font-size: 2rem;
    font-weight: 600;
}
.search_div{
    width: 400px;
    position: relative;
}
.search_div input{
    width: 100%;
    height: 35px;
    padding-left: 5px;
    font-size:1.1rem;
    border-radius: 5px;
    border-style: none;
    background-color:#181818;
    color: white
}
.search_div button{
    position:absolute;
    right:0;
    height: 35px;
    border-style: none;
    cursor: pointer;
}
.avatar_image{
    width: 40px;
    height:40px;
    border-radius: 50%;
}
.form_post_blog .close_form_button .MuiIconButton-label, .form_post_blog #upload_submit_button{
    color: white;
    font-size: 2rem;
}
#upload_submit_button .MuiIconButton-label{
    width:max-content;
    height: max-content;
}

.form_post_blog .close_form_button .MuiButtonBase-root{
    width: 50px;
    height: 50px;
}
.form_post_blog #upload_submit_button{
    display:flex;
    width: 60px;
    height: 60px;
    margin-left:auto;
    margin-right:0;
}
.form_post_blog .close_form_button .MuiButtonBase-root, .form_post_blog #upload_submit_button, .contain_blog .menu, .content .menu{
    background-color:inherit;
    border-radius: 50%  !important;
}
.form_post_blog .close_form_button .MuiButtonBase-root:hover, .form_post_blog #upload_submit_button:hover,.contain_blog .menu:hover, .content .menu:hover{
    background-color:grey;
    transition: all 0.3s ease 0s;
}