.chat_button_group {
    display: flex;
    position: relative;
}

.chat_button_group .background {
    background-color: white;
    border-radius: 10px;
    box-shadow: 0 2.1px 1.3px rgba(0, 0, 0, 0.044), 0 5.9px 4.2px rgba(0, 0, 0, 0.054), 0 12.6px 9.5px rgba(0, 0, 0, 0.061), 0 25px 20px rgba(0, 0, 0, 0.1);
    height: 50px;
    right: -35px;
    position: absolute;
    top: 25px;
    width: 180px;
    cursor: pointer;
}

.chat_button_group .chat-bubble {
    cursor: pointer;
    position: relative;
}

.chat_button_group .bubble {
    transform-origin: 50%;
    transition: transform 500ms cubic-bezier(0.17, 0.61, 0.54, 0.9);
}

.chat_button_group .line {
    fill: none;
    stroke: black;
    stroke-width: 2.75;
    stroke-linecap: round;
    transition: stroke-dashoffset 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.chat_button_group .line1 {
    stroke-dasharray: 60 90;
    stroke-dashoffset: -20;
}

.chat_button_group .line2 {
    stroke-dasharray: 67 87;
    stroke-dashoffset: -18;
}

.chat_button_group .circle {
    fill: black;
    stroke: none;
    transform-origin: 50%;
    transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
}

.active .bubble {
    transform: translateX(24px) translateY(4px) rotate(45deg);
}

.active .line1 {
    stroke-dashoffset: 21;
}

.active .line2 {
    stroke-dashoffset: 30;
}

.active .circle {
    transform: scale(0);
}
